<template>
    <div>
        <table>
            <thead>
                <tr>
                    <th>Address</th>
                    <th>Balance</th>
                </tr>
            </thead>
            <tbody>
                <tr
                    v-for="addressData in addressList"
                    :key="addressData.address"
                >
                    <td>{{ addressData.address }}</td>
                    <td>{{ getAddressBalance(addressData) | toDJTX }} DJT</td>
                </tr>
            </tbody>
        </table>
    </div>
</template>

<script>
import { toDJTX } from '@/helper'
export default {
    filters: {
        toDJTX,
    },
    data() {
        return {
            addressList: [],
        }
    },
    created() {
        this.fetchAddressList()
    },
    methods: {
        async fetchAddressList() {
            try {
                const response = await fetch(
                    'https://djtexplorer.uksouth.cloudapp.azure.com/v2/addresses'
                )
                const data = await response.json()
                this.addressList = data.addresses
            } catch (error) {
                console.error('Error fetching address list:', error)
            }
        },
        getAddressBalance(addressData) {
            const assetKey =
                '2U4xzjHGnxXKWzFKqVZVMEnnepKFRNcBC75346X129Nj7uzyP1'
            return addressData.assets[assetKey].balance
        },
    },
}
</script>

<style>
table {
    border-collapse: collapse;
    width: 100%;
}

th,
td {
    border: 1px solid #ddd;
    padding: 8px;
    text-align: left;
}

th {
    background-color: #f2f2f2;
}
</style>
